@import '../../styles/base';

$background: #10032b;
$black: #000;
$white: #fff;
$dark-grey: #3e434a;
$grey: lightslategray;
$red: #910a00;
$gold: #b59507;





.add-favorites,
.remove-favorites {
    border-color: $black !important;
}

.add-favorites {
    background-color: $dark-grey !important;
}

.remove-favorites {
    background-color: $red !important;
}

.favorite-buttons {
    display: grid;
}